import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/heavy.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia
        sx={{ height: 270 }}
        image={bukiyumi}
        title="MHR ヘビィボウガン"
      />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          ヘビィボウガン
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>火薬と弦を併用した重量級の銃。</h4>
          　高威力の弾を次々に撃ち込む姿は正に移動砲台そのものであり、その火力は絶大の一言。また、剛種武器に搭載可能な、弾発射時の熱を使ってグラビモスの熱線を再現した排熱噴射機構、MHP3以降の一部の銃で可能な、移動を犠牲に一度に大量の弾を装填するしゃがみ撃ち、メゼポルタにて考案された、複数の弾を圧縮し、1発にして撃ち出す圧縮リロードなど、徹底的なまでに火力が追求された武器である。
          <br />
          　しかしその重さ故に機動力は絶望的で、上記の超火力システムは全て大きな欠点が存在する。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
