import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiGL from "./img/light.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia
        sx={{ height: 270 }}
        image={bukiGL}
        title="MHR ライトボウガン"
      />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          ライトボウガン
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>火薬と弦を併用した軽量級の銃。</h4>
          　一発の威力は低いものの、多彩な弾と高い機動力で敵を翻弄出来る。ヘビィボウガンに比べて状態異常弾の扱いに長ける傾向にあり、搦め手の強さはまさに片手剣のガンナー版。抜刀アイテム使用こそできないものの、任意のタイミングでの状態異常発動は非常に強力。
          <br />
          　弾の威力の低さをカバーすべく一部の銃には一定数の弾を高速で連射する速射機能が搭載されており、剛種武器にはそれすら超えた超速射機能が搭載されている。また、メゼポルタでは高速で弾丸を撃ち出す「ジャストショット」が編み出されている。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
