import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/hue.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukiyumi} title="MHR 狩猟笛" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          狩猟笛
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>巨大な笛の形をした打撃武器。あるいは楽器。</h4>
          　太古に用いられたとされる特殊な音楽を奏で、その音で自分や仲間を強化することの出来る異色の武器。
          <br />
          　ハンマーと同じく打撃属性を持ち、頭部へ攻撃すればモンスターを昏倒させることも可能。
          <br />
          　笛であるが故に内部が空洞で、ハンマーと比べ素の衝撃力は落ちるが長いリーチを誇り、奏でる旋律で己を奮い立たせることで武器を担いだまま息も切らさず走り回れるという。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
