import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiGL from "./img/glans.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukiGL} title="MHR ガンランス" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          ガンランス
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>
            とある工房にて開発された武器を発展させ、ランスの先端に砲撃機構を搭載した、浪漫溢れる武器。ランス由来の巨大な盾も健在。
          </h4>
          　
          　武装のさらなる重量化によりランスの持っていた運動性は犠牲となったが、搭載した砲撃機構は硬い甲殻すら物ともせず撃ち抜くことが出来る。さらに、刀身に多大な負荷をかけ威力を高めた砲撃を放つ必殺技「竜撃砲」まで実装している。
          <br />
          　ガンランスは砲撃による斬れ味磨耗、変則的な斬撃など扱うべき事柄は多い。しかし武器を知り、相手を知り、使いこなすことができれば、必ず狩猟の糧となるだろう。
          <br />
          　長らく砲撃機構は爆風しか発生しなかったが、近年ではモンスターの体表に食い込んだ後爆発する特殊な炸裂弾頭を射出する「竜杭砲」機構が開発された。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
