import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/lans.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukiyumi} title="MHR ランス" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          ランス
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>鋭く巨大な槍と、これまた巨大な盾のセット武器。</h4>
          　武装の重量により抜刀時の移動や納刀は極めて遅いが、巨大な盾による守りは正に鉄壁。
          <br />
          　ピンポイントの素早い突きを連続で放ち、攻撃面でも隙がない。スタミナを消費することで、専用技「突進」で高速移動しながら攻撃を仕掛けたり、方向順不同で3連続で出せるステップで回避と位置調整を兼ねたりと、使いこなせば普段の鈍重さからは想像出来ない運動性を発揮する。
          <br />
          　様々なガードアクションを駆使することで、流れるようにカウンターを繰り出したり、ガードしながら死地に飛び込むなど、徹底的に相手のターンに割り込む戦法を得意とする。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
