import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/tachi.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukiyumi} title="MHR 太刀" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          太刀
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>大剣から派生した細身の両手剣。</h4>
          　軽量化された刀身は流れるような連撃を可能としており使い手は特殊な気「練気」を練り、刃に乗せて「気刃」を放つ。
          <br />
          　特殊な一閃を放つことで"練気"をオーラのように刀身に纏わせるため、達人の振るう太刀は赤く染まるという。
          <br />
          　攻撃を直接受け止めることはできないが、居合いの構えから繰り出す抜刀技はモンスターの攻撃を受け流すことすら可能。
          <br />
          　纏ったオーラを一気に放出することで、一閃した箇所を千々に切り裂く"気刃兜割"を繰り出すことができる。
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
