import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/caxe.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia
        sx={{ height: 270 }}
        image={bukiyumi}
        title="MHR チャージアックス"
      />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          チャージアックス
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>バルバレギルドで開発された機械武器。</h4>
          　右手に盾を左手に剣を持ち、機動力と防御を備えた剣モードと合体により、盾を刃として用いる斧モードの2形態を持つ。
          <br />
          　最大の特徴は剣撃エネルギーを内蔵ビンに蓄積し、一気に解放するチャージ機構。
          <br />
          　蓄積したエネルギーはモンスターの甲殻に撃ち込み爆発させる「属性解放斬り」として利用する。さらに、盾へ圧縮エネルギーを付与し爆発反応装甲として利用する属性強化や、剣に付与する剣属性強化変形機構をオーバーロードさせ斧を高速回転させる斧強化などの手法が開発されている。エネルギーを圧縮し、一気に解放する"超高出力属性解放斬り"は圧巻の一言。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
