import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/yumi.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukiyumi} title="MHR 弓" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          弓
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>弦のみを用いて矢を放つ武器。</h4>
          　スタミナと引き換えに弓を引き絞り、引き絞った時間で攻撃性能が変化する。付かず離れず適正な距離を見定めながら戦う必要があるが、それが可能なだけの機動力はある。
          <br />
          　ピンポイントに狙える腕が必要だが、それが出来ればその火力はヘビィボウガンに匹敵する。
          <br />
          　矢を用いた近接攻撃もガンナーのものとしては強力で、小型モンスターともある程度渡り合える。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
