import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/saxe.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia
        sx={{ height: 270 }}
        image={bukiyumi}
        title="‘MHR スラッシュアックス"
      />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          スラッシュアックス
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>ロックラックにて開発された変形機構を搭載した戦斧。</h4>
          　大型武器の双極、大斧と大剣に変形する、攻めに特化した可変武器。その代償として身を守るすべを持たず、特性の異なる二つの武器を操る事になるため扱いが難しいが、熟練者はまるでモンスターの攻撃をすり抜けるかのように闘うという。
          <br />
          　ビンに仕込まれた特殊な薬液の効果を発揮する剣モードを繰り返し使用することで、短時間だが薬液が武器表面で活性化し続け、傷口を炸裂させる高出力状態となる。
          <br />
          　近年では空気中では拡散してしまうエネルギーを地面に打ち込み爆発させる"真属性解放突き"や、モンスターに突き刺し爆発させる"零距離解放突き"といった技法が編み出された。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
