import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/musi.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukiyumi} title="MHR 操虫棍" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          操虫棍
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>
            バルバレギルド管轄域にてその存在が公になった狩猟猟虫と呼ばれる虫と、切断属性の棍の2つを操る武器。
          </h4>
          　特殊な印や虫笛を使い猟虫を操り、近接武器ながら遠隔攻撃ができるだけに止まらず、棍を使って跳び上がり、射出機構を利用して空中を跳ぶなど非常にアクロバティックな動作が特徴。
          <br />
          　また、猟虫が採取したエキスを身体に取り込むと身体能力が強化され棍を片手で自在に振り回し、強力な連続攻撃を繰り出せるようになるほか少々の攻撃や咆哮を無視できる耐久性をつけることまでできる。
          <br />
          　近年では猟虫と同時に攻撃する技も考案された他、モンスターを足場に宙を舞うこともできるようだ。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
