import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/katate.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukiyumi} title="MHR 片手剣" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          片手剣
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>
            小振りな短剣と盾のセット武器。左手に剣を、右手に盾を装備する。
          </h4>
          　リーチが短くモーションに癖が大きいが、様々な作品で初期武器となっている。
          <br />
          　跳躍やバックステップなど、高い機動力を活かした臨機応変な戦いが持ち味で属性の扱いに長ける一方盾を使った打撃も得意であり、サポートもアタッカーも柔軟にこなす。
          <br />
          　一撃の威力こそ低いが、熟達すると恐るべき手数で高い火力を発揮する。非常に軽量かつハンターの動きを妨害しないため、武器を構えたままアイテムが使用可能である。
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
