import "./styles.css";
import B_Yumi from "./B_Yumi";
import B_Taiken from "./B_Taiken";
import B_GL from "./B_GL";
import B_Hanma from "./B_Hanma";
import B_Katate from "./B_Katate";
import B_Souken from "./B_Souken";
import B_Tachi from "./B_Tachi";
import B_Hue from "./B_Hue";
import B_Lans from "./B_Lans";
import B_Axe from "./B_Axe";
import B_Caxe from "./B_Caxe";
import B_Souchu from "./B_Souchu";
import B_Light from "./B_Light";
import B_Heavy from "./B_Heavy";

import Conteainer from "@mui/material/Card";

export default function App() {
  return (
    <div className="App">
      <h1>Welcom to Monster Hunter BUKI Shop</h1>
      <h2>近接武器</h2>

      <Conteainer sx={{ display: "flex" }}>
        <B_Taiken />
        <B_Tachi />
        <B_Katate />
        <B_Souken />
      </Conteainer>

      <Conteainer sx={{ display: "flex" }}>
        <B_Hanma />
        <B_Hue />
        <B_Lans />
        <B_GL />
      </Conteainer>

      <Conteainer sx={{ display: "flex" }}>
        <B_Axe />
        <B_Caxe />
        <B_Souchu />
      </Conteainer>

      <h2>遠距離武器</h2>

      <Conteainer sx={{ display: "flex" }}>
        <B_Light />
        <B_Heavy />
        <B_Yumi />
      </Conteainer>
    </div>
  );
}
