import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukiyumi from "./img/hanma.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukiyumi} title="MHR ハンマー" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          ハンマー
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>重量級の鈍器。</h4>
          　攻撃速度は劣悪で隙も大きいが一撃が重く、強大なモンスターも頭部への強烈な打撃を繰り返されれば昏倒は免れない。
          <br />
          　リーチは短いが、代わりに機動力が重量級にしては高めで比較的動きやすい。さらに大剣と異なるのが溜め攻撃。スタミナを消費しつつ力を溜め、さらに溜めながら移動が可能である。
          <br />
          　溜めはじめた場所から動けるため弱点部位を狙いやすく、そこから放たれる一撃は大きなダメージと衝撃、頭部に当てれば昏倒値の蓄積を生む。
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
