import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import bukitaiken from "./img/taiken.webp";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 512 }}>
      <CardMedia sx={{ height: 270 }} image={bukitaiken} title="MHR 大剣" />
      <CardContent>
        <Typography
          textAlign="center"
          gutterBottom
          variant="h5"
          component="div"
        >
          大剣
        </Typography>
        <Typography variant="body2" color="text.secondary">
          <h4>ハンターの身の丈程もある巨大な両手剣。</h4>
          　攻撃速度と機動力を犠牲に、長物のリーチと重量を活かした破壊力で勝負する武器。
          <br />
          　納刀自体は早めであり、それを利用した一撃離脱戦法では事実上の機動力も高い。
          足が止まるというデメリットと引き換えに、一部の攻撃時に力を溜めることが可能であり、その破壊力は他の追随を許さない。
          <br />
          　斬れ味が落ちるという欠点付きで、幅広の刀身を利用したガードも可能で緊急時の生存力も高い。
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Add to Cart</Button>
        <Button size="small">Buy now</Button>
      </CardActions>
    </Card>
  );
}
